<template>
	<div class="template-list-container">
		<div class="template-list-content">
			<div class="template-new-card" v-for="item in templateListLength" :key="item">
				<template-card-item :obj="templateList[item-1]"></template-card-item>
			</div>
		</div>
		<div class="template-list-footer">
			<div class="template-list-footer__text">
				搜索结果不满意？换个关键词试试～ 或 
				<a>提交需求</a>
			</div>
			<pagination :totalCount="templateTotal"></pagination>
		</div>
	</div>
</template>

<script>
	import Pagination from '@/components/common/pagination/Pagination.vue'
	import TemplateCardItem from '@/components/content/TemplateCardItem/TemplateCardItem.vue'
	export default {
		name: 'TemplateTable',
		components: {
			Pagination,
			TemplateCardItem
		},
		props: {
			templateData: null
		},
		computed: {
			templateList() {
				return this.templateData.data
			},
			templateListLength() {
				let length;
				if(this.templateData != ''){
					length =  this.templateList.length;
				}
				return length;
			},
			templateTotal() {
				return this.templateData.TemplateTotal
			}
		}
	}
</script>

<style scoped>
	.template-list-container {
		
	}
	.template-list-content {
		display: flex;
		flex-wrap: wrap;
	}
	.template-new-card{
		width: calc((100% - 80px) / 5);
		margin-right: 20px;
		margin-bottom: 21px;
	}
	.template-new-card:nth-child(5n+5) {
		margin-right: 0px;
	}
	.template-list-footer {
		display: flex;
		justify-content: space-between;
		margin-top: 42px;
		font-size: 20px;
	}
	.template-list-footer__text {
		font-size: 14px;
	}
	.template-list-footer__text a {
		color: var(--color-text-change);
	}
</style>
