<template>
	<ul class="pagination clearfix">
		<li><a title="上一页" @click="currentPage<=1?'':pageClick(currentPage-1)">上一页</a></li>
		<li v-for="i in showPageNums" :key="i">
			<a v-if="i>0" @click="pageClick(i)" :title="i" ref="a" :class="{'active': i == currentPage}">{{i}}</a>
			<a v-else-if="i === '-'" @click="pageClick(currentPage-5)" title="向前5页" >...</a>
			<a v-else-if="i === '+'" @click="pageClick(currentPage+5)" title="向后5页">...</a>
		</li>
		<li><a title="下一页" @click="currentPage>=totalCount?'':pageClick(currentPage+1)">下一页</a></li>
	</ul>
</template>

<script>
	export default {
		name: 'Pagination',
		data() {
			return {
				currentPage: 1
			}
		},
		props: {
			totalCount: {
				type: Number,
				default: 0
			}
		},
		computed: {
			showPageNums() {
				let pageNum = this.totalCount,
						index = this.currentPage;
				if(pageNum <=5) return [1];
				
				if(index < 4) return [1, 2, 3, 4, '+', pageNum];
				else if (index >= pageNum-3) return [1, '-', pageNum-3, pageNum-2, pageNum-1, pageNum];
				return [1, '-', index-2, index-1, index, index+1, index+2, '+', pageNum];
			}
		},
		methods: {
			pageClick(index) {
				if(index < 1){
					this.currentPage = 1;
				}else if(index > this.totalCount) {
					this.currentPage = this.totalCount;
				}else {
					this.currentPage = index;
				}
			}
		}
	}
</script>

<style scoped>
	.pagination {
		font-variant: tabular-nums;
		font-feature-settings: "tnum";
	}
	.pagination>li {
		float: left;
		margin-right: 25px;
	}
	.pagination>li:last-child {
		margin-right: 0;
	}
	.active {
		color: var(--color-text-change);
	}
</style>
