<template>
	<div class="search-bar" ref="searchBar">
		<input type="text" :placeholder="placeholder"  v-model="searchTrems"/>
		<span>
			<button @mouseover="btnHover" @mouseleave="btnLeave" @click="seacrh">{{btnText}}</button>
		</span>
	</div>
</template>

<script>
	export default {
		name: 'SearchBar',
		data() {
			return {
				searchTrems: ''
			}
		},
		props: {
			placeholder: {
				type: String,
				default: '搜索海量模板，拿来即用'
			},
			btnText: {
				type: String,
				default: '搜索'
			},
			height: {
				type: Number,
				default: 40
			},
			width: {
				type: Number,
				default: 580
			} ,
			searchColor: {
				type: String,
				default : 'rgb(255, 123, 51)'
			} 
		},
		methods: {
			btnHover() {
				let searchBar_Button = this.$refs.searchBar.childNodes[1].childNodes[0].style;
				searchBar_Button.backgroundColor = this.searchColor;
			},
			btnLeave() {
				let searchBar_Button = this.$refs.searchBar.childNodes[1].childNodes[0].style;
				searchBar_Button.backgroundColor = 'rgba('+ this.searchColor.split('(')[1].split(')')[0] +', .9)';
			},
			seacrh() {
				this.$emit('search', this.searchTrems);
			}
		},
		mounted() {
			//搜索框和按钮的样式
			let searchBar = this.$refs.searchBar;
			let searchBar_Input = searchBar.childNodes[0].style;
			let searchBar_Button = searchBar.childNodes[1].childNodes[0].style;
			// 宽度/最小宽度
			searchBar_Input.minWidth = this.width + 'px';
			searchBar_Button.minWidth = this.width * 0.13 + 'px';
			//高度
			searchBar.style.minHeight = this.height + 'px';
			searchBar_Input.height = this.height + 'px';
			searchBar_Button.height = this.height + 'px';
			// 搜索框的padding
			searchBar_Input.padding = '0 ' + this.height/2 + 'px';
			// 圆角边框
			searchBar_Input.borderTopLeftRadius = this.height/2 + 'px';
			searchBar_Input.borderBottomLeftRadius = this.height/2 + 'px';
			searchBar_Button.borderTopRightRadius = this.height/2 + 'px';
			searchBar_Button.borderBottomRightRadius = this.height/2 + 'px';
			// 边框颜色 + 按钮背景颜色
			searchBar_Input.border = '1px solid ' + this.searchColor;
			searchBar_Button.backgroundColor = 'rgba('+ this.searchColor.split('(')[1].split(')')[0] +', .9)';
			
		}
	}
</script>

<style>
	.search-bar {
		display: inline-block;
	}
	.search-bar>span button {
		display: inline-block;
		padding: 0 15px;
		border: 0;
		color: #fff;
	}
	.search-bar>input:focus, .search-bar>span button:focus{
		outline: none;
	}
</style>
