<template>
	<div class="main-tags">
		<div class="main-tags-selector"  v-for="(item,tagTypeIndex) in tags" :key="item.id">
			<p class="main-tags-selector__type">{{item.name}}</p>
			<div class="main-tags-selector__list">
				<a class="main-tags-selector__list-tag" :class="{'selected': 0 == tagsIndex[tagTypeIndex]}" @click="tagClick(tagTypeIndex, 0)">全部</a>
				<div class="warp" v-for="(value, tagIndex) in item.values" :key="value">
					<a class="main-tags-selector__list-tag" :class="{'selected': tagIndex+1 == tagsIndex[tagTypeIndex]}" @click="tagClick(tagTypeIndex,tagIndex+1)">{{value}}</a>
				</div>
				<div v-if="isShow[tagTypeIndex]" class="template-tags-selector__list-more">
					<icon-btn btnText="更多" iconClass="gd-computer" iconBtnType='right'></icon-btn>
				</div>	
			</div>
		</div>
	</div>
</template>

<script>
	import IconBtn from '@/components/common/iconbtn/IconBtn'
	export default {
		name: 'MainTags',
		components: {
			IconBtn
		},
		data() {
			return {
				isShow: [false, false, false],
				tagsIndex: [0,0,0]
			}
		},
		props: {
			tags: null
		},
		computed: {
			
		},
		methods: {
			tagClick(tagTypeIndex, tagIndex) {
				this.$set(this.tagsIndex, tagTypeIndex, tagIndex);
			}
		}
	}
</script>

<style scoped>
	.main-tags {
		padding: 28px 28px 13px;
		border-radius: 2px;
		border-top: 50px solid rgb(255, 133, 51);
	}
	.main-tags-selector {
		display: flex;
		/* position: relative; */
	}
	.main-tags-selector__type {
		flex-shrink: 0;
		margin-top: 6px;
		padding-right: 30px;
		color: #666;
	}
	.main-tags-selector__list {
		/* 高度可变--待实现 */
		height: 32px;
		margin-bottom: 10px;
		padding-right: 60px;
		overflow: hidden;
	}
	.height {
		height: 37px;
	}
	.main-tags-selector__list .warp {
		display: inline-block;
	}
	.main-tags-selector__list-tag {
		display: inline-block;
		padding: 6px 14px;
		margin-right: 6px;
		margin-bottom: 9px;
	}
	.selected{
		border-radius: 16px;
		background-color: #e0f2ff;
		color: #2875e8;
	}
	.template-tags-selector__list-more {
		position: absolute;
		top: 0px;
		right: 0px;
		height: 30px;
		text-align: center;
	}
</style>
