<template>
	<div class="nav-bar" :style="style">
		<div class="nav-bar__left"><slot name="left"></slot></div>
		<div class="nav-bar__center"><slot name="center"></slot></div>
		<div class="nav-bar__right"><slot name="right"></slot></div>
	</div>
</template>

<script>
	export default {
		name: 'NavBar',
		props: {
			navBarWidth: {
				type: String,
				default: '100%'
			}
		},
		computed: {
			style() {
				return 'width: ' + this.navBarWidth;
			}
		}
	}
</script>

<style>
	.nav-bar{
		display: flex;
		line-height: 48px;
		text-align: center;
		margin: 0 auto;
		/* box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .1); */
	}
	.nav-bar__center{
		flex: 1;
	}
</style>
