<template>
	<div class="template-list-container">
		<nav-bar class="title-banner" ref="titleBanner" :class="{'isTop':searchBarIsTop}">
			<search-bar slot="center" @search="searchLoading" ref="searchBar"></search-bar>
		</nav-bar>
		<div class="hot-categorites">
			<span>热门搜索:</span>
			<a v-for="(item, index) in hotCategorites" :key='index.name' href="#">{{item}}</a>
		</div>
		<div class="template-list-content">
			<div class="template-main">
				<main-tags :tags="templateTags"></main-tags>
			</div>
			<div class="template-content__sort">
				<span>排序:</span>
				<a v-for="(item, index) in sorts" :key="item.name" :class="{'selected': index === sortIndex}" @click="sortClick(index)">{{item.name}}</a>
			</div>
			<div class="template-content__results">
				<!-- 模板列表 -->
				<template-table :templateData="templateData"></template-table>
			</div>
		</div>
	</div>
</template>

<script>
	//components
	import NavBar from '@/components/common/navbar/NavBar.vue'
	import SearchBar from '@/components/common/searchbar/SearchBar'
	//childComps
	import MainTags from './childComps/MainTags.vue'
	import TemplateTable from './childComps/TemplateTable.vue'
	
	import {
		getTemplateTags,
		getTemplateList
	} from '@/network/template.js'
	
	export default {
		name: 'TemplateList',
		components: {
			SearchBar,
			NavBar,
			TemplateTable,
			MainTags
		},
		data() {
			return {
				searchBarIsTop: false,
				hotCategorites: ['调查问卷', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名', '活动报名'],
				sorts: [
					{
						name: '综合推荐'
					},
					{
						name: '最多选用'
					},
					{
						name: '最新上传'
					}
				],
				sortIndex: 0,
				templateTags: '',
				templateData: ''
			}
		},
		methods: {
			/**
			 * 排序
			 * @param {Object} index
			 */
			sortClick(index) {
				this.sortIndex = index
			},
			/**
			 * 标签栏
			 */
			getTemplateTags() {
				getTemplateTags().then(res => {
					this.templateTags = res;
				})
			},
			/**
			 * 模板列表
			 */
			getTemplateList() {
				getTemplateList().then(res => {
					this.templateData = res;
				})
			},
			/**
			 * 搜索
			 * @param {Object} value
			 */
			searchLoading(value) {
				console.log(value);
			},
			handleScorllY() {
				if(this.$refs.searchBar != null) {
					if(this.$refs.searchBar.$el.getBoundingClientRect().top == 0) {
						this.searchBarIsTop = true;
					}else {
						this.searchBarIsTop = false;
					}
				}
			}
		},
		created() {
			this.getTemplateTags();
			this.getTemplateList();
		},
		mounted() {
			window.addEventListener('scroll', this.handleScorllY, true)
		},
		beforeDestroy() {
			window.removeEventListener('scroll', this.handleScorllY());
		}
	}
</script>

<style scoped>
	/* template-list容器 */
	.template-list-container {
		background-color: #fff;
	}
	/* 搜索栏 */
	.title-banner {
		position: absolute;
		z-index: 100;
		position: sticky;
		background-color: #fff;
		top: 0px;
		box-shadow: 0 -1px #fff;
	}
	.isTop {
		box-shadow: 0 -1px 5px 0 rgba(0, 0, 0, 0.15);
	}
	/* 热门搜索 */
	.hot-categorites{
		margin: 0 auto;
		width: 600px;
		height: 24px;
		margin-top: 14px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 12px;
	}
	.hot-categorites>span, .hot-categorites>a {
		margin-right: 14px;
	}
	
	/* template主体内容 */
	.template-list-content {
		width: 1200px;
		margin: 0 auto;
	}
	.template-main {
		margin-top: 20px;
	}
	.template-main, .template-content__sort {
		background-color: var(--color-background);
	}
	.template-content__sort {
		margin: 14px 0 28px;
		padding: 0 28px;
		line-height: 54px;
	}
	.template-content__sort>span{
		margin-right: 14px;
	}
	.template-content__sort>a {
		margin-right: 21px;
	}
	.template-list-container >>> .selected {
		color: var(--color-text-change);
	}
</style>
